<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/friendly/list"  @click="rest" type='list'>
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt collapse='true'>
        <li>设置为启用，前台可显示友情链接地址</li>
      </cms-prompt>
      <!-- 表单里面有上传的一定要阻止掉页面刷新 -->
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px"  @submit.native.prevent>
        <el-form-item label="名称" prop="linkName" :error='errorMessage.linkName'>
           <el-input  :autofocus="true" ref="first" v-model="formData.linkName"></el-input>
        </el-form-item>
        <el-form-item label="URL" prop="linkUrl"  :error='errorMessage.linkUrl' >
          <el-input v-model="formData.linkUrl" maxlength="50"></el-input>
        </el-form-item>
        <el-form-item label="是否启用" prop="isEnable">
          <cms-switch  v-model="formData.isEnable" ></cms-switch>
        </el-form-item>
        <el-form-item label="Logo" prop="linkLogo" :error='errorMessage.linkLogo' class="brands-logo">
          <cms-picture-zone label='上传图片' @change="getLinkLogo" :data='formData.resourcesSpaceData'
            v-if="status" >
          </cms-picture-zone>
        </el-form-item>
        <el-form-item label="排序值" prop="sortNum" :error='errorMessage.sortNum'>
          <el-input v-model="formData.sortNum" maxlength="2"></el-input>
        </el-form-item>
        <el-form-item label="备注信息" prop="remark" :error="errorMessage.remark">
          <el-input type="textarea" v-model="formData.remark" maxlength="200"></el-input>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
         <cms-button @click="update"  v-color >保存修改</cms-button>
        <cms-button type='rest' @click="reset">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { friendLinkUpdate, friendLinkGet } from '@/api/setting/friendlyLink.js'

var defaultData = {
  'linkName': '',
  'linkUrl': '',
  'isEnable': '',
  'linkLogo': '',
  'enabled': '',
  'sortNum': '',
  'remark': ''
}
export default {
  name: 'friendlyEdit',
  mixins: [form],
  data() {
    return {
      status: true,
      formData: Object.assign({}, defaultData),
      rules: {// 修改规则
        linkName: [rules.required('请填写名称'), rules.len(1, 150)],
        linkUrl: [rules.required('请填写URL'), rules.url(), rules.len(1, 150)],
        linkLogo: [rules.required('必须选择一张图片', 'change')],
        sortNum: [rules.required('请填写排序值'), rules.number()],
        remark: [rules.len(1, 500)]
      }
    }
  },
  methods: {
    getLinkLogo(items) {
      if (items) {
        this.formData.linkLogo = items[0].id
      } else {
        this.formData.linkLogo = ''
      }
      this.$refs.form.validateField('linkLogo')
    },
    getFormData(id) { // 通过id获取表单信息
      friendLinkGet({ id: id }).then(res => {
        this.formData = Object.assign(defaultData, res.data)
      })
    },
    update() {
      this.updateForm(friendLinkUpdate, this.formData, '/friendly/list', 'list')
    },
    reset() {
      this.getFormData(this.$route.query.id)
      this.$refs.form.resetFields()
    }
  },
  created() {
    this.getFormData(this.$route.query.id)
  }
}
</script>




